import { h } from '@externs/preact'
import { loadStyle } from '@lemuria/load-scripts'

export default class Logo {
  static 'load'(cb) {
    cb(null)
    loadStyle('/css/Logo.css', () => {})
  }
  static get 'plain'() {
    return true
  }
  /**
   * @param {Object} params
   * @param {Splendid} params.splendid
   */
  serverRender({ splendid, sizes = '300px' }) {
    splendid.export()
    splendid.css('styles/Logo.css', null, {
      dynamic: true,
      preload: true,
    })

    return (h('splendid-img',{ 'sizes':sizes,'id':"Logo", 'above-fold':true, 'responsive':true, 'webp':"text", 'no-potrace':true, 'src':"img/logo.png", 'alt':"Art Deco Logo", 'className':"img-fluid" }))
  }
  render() {}
}


/**
 * @typedef {import('splendid/src/Splendid').default} Splendid
 */